<template>
  <v-card raised elevation="12">
    <v-img width="300" class="mx-auto" src="@/assets/login_logo.svg"></v-img>
    <v-card-text>
      <h1 color="primary">Iniciar sesión</h1>
    </v-card-text>
    <v-card-subtitle>Ingresa con tu número teléfonico</v-card-subtitle>
    <v-card-actions>
      <v-col>
        <v-form ref="form" v-model="valid">
          <v-text-field
            v-on:keydown.enter.prevent="getVerificationCode"
            type="number"
            label="Escribe tu número"
            :counter="10"
            :rules="phoneRules"
            prefix="+52"
            outlined
            required
            v-model="phoneNumber"
          ></v-text-field>
          <v-btn
            type="button"
            :disabled="!valid"
            raised
            color="primary"
            @click="getVerificationCode"
            large
            block
          >Enviar</v-btn>
        </v-form>
      </v-col>
    </v-card-actions>
    <v-card-text>
      <v-label>Enviaremos un mensaje a este número con un código de confirmación</v-label>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data: function() {
    return {
      valid: true,
      phoneNumber: null,
      phoneRules: [
        v => !!v || "El teléfono es requerido",
        v => (v && v.length <= 10) || "El teléfono debe ser de 10 digitos"
      ]
    };
  },
  methods: {
    ...mapActions("login", ["getVerificationCodeAction"]),
    getVerificationCode() {
      console.log("code")
      if (this.$refs.form.validate()) {
        
        this.getVerificationCodeAction(this.phoneNumber);
      }
    },
    submit(event) {
      event.target.blur();
      this.getVerificationCode()
    }
  }
};
</script>

<style lang="scss">
.img {
  width: 250px;
  margin: auto;
  background: var(--color-primary);
  border-radius: 25px;
}
</style>